import { GoogleLogin, useGoogleLogin } from "react-google-login";
import { setAccessToken } from "../../accessToken";
import { checkAuth } from "../../CheckAuth";
import { useLoginMutation } from "../../generated/graphql";
import "../../css/navbar.scss";
import { useEffect } from "react";
import M from "materialize-css";
import AnnouncementBar from "./AnnouncementBar";
import Logo from "../../images/Blank_Logo.png";
const Navbar = () => {
    useEffect(() => {
        var elems = document.querySelectorAll(".sidenav");
        M.Sidenav.init(elems);
    });
    const [login] = useLoginMutation();

    const handler = async res => {
        console.log("res :>> ", res);
        let response = await login({
            variables: {
                user_id: res.googleId,
                email: res.profileObj.email,
            },
        });

        console.log("response :>> ", response.data?.login);
        setAccessToken(response.data?.login.accessToken!);
        localStorage.setItem("iru5", response.data?.login.refreshToken!);
        window.location.href = "#/my-orders";
    };

    const { signIn } = useGoogleLogin({
        clientId:
            "36358700157-4ihpf9tj4bjf4k346b8pnubt4meim4j0.apps.googleusercontent.com",
        onSuccess: handler,
    });

    // const logoutHandler = () => {
    //     localStorage.setItem("iru5", "");
    //     setAccessToken("");
    //     window.location.reload();
    // };

    console.log("checkAuth() :>> ", checkAuth());
    return (
        <header style={{ position: "sticky", top: 0, zIndex: 997 }}>
            <AnnouncementBar />
            <div>
                <nav className="white">
                    <div className="nav-wrapper">
                        <a href="#/home" className="brand-logo">
                            <img
                                style={{
                                    width: "250px",
                                    height: "64px",
                                }}
                                src={Logo}
                                alt="Nari-Store"
                            ></img>
                        </a>
                        <a
                            data-target="mobile-demo"
                            className="sidenav-trigger left"
                        >
                            <i className="material-icons black-text noselect">
                                menu
                            </i>
                        </a>

                        <ul
                            id="nav"
                            className="center  hide-on-med-and-down"
                            style={{
                                left: "50%",
                                transform: "translateX(-50%)",
                                display: "inline-block",
                                position: "absolute",
                                minWidth: "600px",
                            }}
                        >
                            <li>
                                <a href="#/home">HOME</a>
                            </li>
                            <li className="black-text">
                                <a href="#/products">PRODUCTS</a>
                            </li>

                            <li className=" black-text">
                                {checkAuth() ? (
                                    <a href="#/my-orders">MY ORDERS</a>
                                ) : (
                                    // eslint-disable-next-line
                                    <a
                                        onClick={async () => {
                                            await signIn();
                                        }}
                                    >
                                        MY ORDERS
                                    </a>
                                )}
                            </li>

                            {/* {!checkAuth() ? (
                                <li className="right black-text">
                                    <GoogleLogin
                                        clientId="36358700157-4ihpf9tj4bjf4k346b8pnubt4meim4j0.apps.googleusercontent.com"
                                        onSuccess={async res => {
                                            await handler(res);
                                            window.location.reload();
                                        }}
                                        onFailure={handler}
                                    />
                                </li>
                            ) : (
                                <></>
                            )} */}

                            <li>
                                <a href="#/my-cart">
                                    CART
                                    {/* <i className="material-icons">
                                        shopping_cart
                                    </i> */}
                                </a>
                            </li>
                        </ul>
                        <a
                            href="#/my-cart"
                            className="right hide-on-large"
                            style={{ color: "#000" }}
                        >
                            CART
                            {/* <i className="material-icons">
                                        shopping_cart
                                    </i> */}
                        </a>
                    </div>
                </nav>
            </div>
            <ul
                className="sidenav"
                id="mobile-demo"
                style={{
                    backgroundColor: "rgba(0, 0, 0, 0.95)",
                    paddingTop: "42px",
                }}
            >
                <li>
                    <a
                        style={{ color: "#fff", textAlign: "center" }}
                        href="#/products"
                    >
                        Products
                    </a>
                </li>
                <li style={{}}>
                    {checkAuth() ? (
                        <a
                            style={{ color: "#fff", textAlign: "center" }}
                            href="#/my-orders"
                        >
                            My Orders
                        </a>
                    ) : (
                        // eslint-disable-next-line
                        <a
                            style={{ color: "#fff", textAlign: "center" }}
                            onClick={async () => {
                                await signIn();
                            }}
                        >
                            My Orders
                        </a>
                    )}
                </li>
                {/* {!checkAuth() ? (
                    <li>
                        <GoogleLogin
                            clientId="36358700157-4ihpf9tj4bjf4k346b8pnubt4meim4j0.apps.googleusercontent.com"
                            onSuccess={async res => {
                                await handler(res);
                                window.location.reload();
                            }}
                            onFailure={handler}
                        />
                    </li>
                ) : (
                    <></>
                )} */}
            </ul>
        </header>
    );
};

export default Navbar;
