import { useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import { useCheckoutMutation } from "../../generated/graphql";
import { changeQuantityOnProduct } from "../../redux/actions/userAction";

const Cart = () => {
    const [checkout] = useCheckoutMutation();
    const [state, setState] = useState({
        refresh: false,
        quantities: [],
    });

    const products = useSelector(state => state.productsInCart);
    const dispatch = useDispatch();
    console.log("products cart :>> ", products);

    let total = 0;
    for (let i = 0; i < products.length; i++) {
        total += products[i].price * products[i].quantity;
    }

    return (
        <div>
            {products.length === 0 ? (
                <h3 className="center-align">Your Shopping Cart Is Empty!</h3>
            ) : (
                <>
                    <h3 className="center-align">Subtotal</h3>
                    <h6 className="center-align">
                        ${Number(total / 100).toFixed(2)}
                    </h6>
                    {products.map((_val, i) => {
                        return (
                            <>
                                <div
                                    id={`product-${i}`}
                                    className={`product-${i} row container`}
                                >
                                    <div className={` col s4`}>
                                        <img
                                            src={products[i].img_url}
                                            style={{ width: "100%" }}
                                        />
                                    </div>
                                    <div
                                        className="col s4"
                                        style={{
                                            height: "100%",
                                            display: "flex",
                                            flexDirection: "column",
                                            justifyContent: "center",
                                        }}
                                    >
                                        <h6>{products[i].name}</h6>
                                    </div>

                                    <div className="col s2">
                                        <input
                                            type="text"
                                            value={
                                                state.quantities[i] ||
                                                state.quantities[i] === 0
                                                    ? state.quantities[i]
                                                    : products[i].quantity
                                            }
                                            minLength={0}
                                            maxLength={2}
                                            pattern="\D"
                                            onBlur={e => {
                                                dispatch(
                                                    changeQuantityOnProduct(
                                                        Number(e.target.value),
                                                        i
                                                    )
                                                );

                                                let tmp = state.quantities;
                                                tmp.splice(i, 1);

                                                // if (e.target.value === "0") {
                                                //     anime({
                                                //         duration: 400,
                                                //         targets: `.product-${i}`,
                                                //         easing: "easeOutExpo",
                                                //         opacity: {
                                                //             value: [1, 0],
                                                //             easing: "linear",
                                                //         },
                                                //     });
                                                // }
                                                setState({
                                                    ...state,
                                                    quantities: tmp,
                                                    refresh: !state.refresh,
                                                });
                                            }}
                                            onChange={e => {
                                                let tmp: any = state.quantities;

                                                if (!e.target.value) {
                                                    tmp[i] = 0;
                                                    setState({
                                                        ...state,
                                                        quantities: tmp,
                                                    });
                                                }
                                                if (
                                                    e.target.value.match(/\d/g)
                                                ) {
                                                    tmp[i] = Number(
                                                        e.target.value
                                                    );
                                                    setState({
                                                        ...state,
                                                        quantities: tmp,
                                                    });
                                                }
                                            }}
                                            style={{
                                                textAlign: "center",
                                                border: "1px solid #000",
                                                height: "56px",
                                                width: "56px",
                                            }}
                                        />
                                    </div>

                                    <div className="col s2">
                                        <span></span>$
                                        {Number(
                                            products[i].price / 100
                                        ).toFixed(2)}
                                    </div>
                                </div>
                            </>
                        );
                    })}
                    <div
                        style={{
                            width: "100%",
                            display: "flex",
                            justifyContent: "center",
                            marginBottom: "40px",
                        }}
                    >
                        <a
                            className="btn"
                            style={{
                                marginTop: "16px",
                                width: "380px",
                                backgroundColor: "#343145",
                            }}
                            href="#/checkout"
                        >
                            checkout
                        </a>
                    </div>
                </>
            )}
        </div>
    );
};

export default Cart;
