import CopyrightBar from "./CopyrightBar";
import FooterLogo from "../../images/Blank_Footer_logo.png";
import { FaInstagram, FaEnvelope } from "react-icons/fa";

const Footer = () => {
    return (
        <>
            <footer
                style={{
                    backgroundColor: "#000",
                    width: "100%",
                    height: "220px",
                }}
            >
                <div className="container">
                    <div
                        className="row"
                        style={{
                            width: "100%",
                            display: "flex",
                            justifyContent: "space-between",
                        }}
                    >
                        <div className="col l6 s12">
                            <img
                                src={FooterLogo}
                                alt="footer_logo"
                                style={{ height: "220px" }}
                            />
                        </div>

                        <div className="col l4 offset-12 s12">
                            <ul>
                                <li>
                                    <a href="" className="white-text">
                                        <h4 style={{ fontWeight: 600 }}>
                                            Return Policy
                                        </h4>
                                    </a>
                                </li>
                                <li>
                                    <a href="#/contact" className="white-text">
                                        <h4
                                            style={{
                                                marginTop: "0px",
                                                fontWeight: 600,
                                            }}
                                        >
                                            Contact Us
                                        </h4>
                                    </a>
                                </li>
                                <li>
                                    <a href="#/FAQ" className="white-text">
                                        <h4
                                            style={{
                                                marginTop: "0px",
                                                fontWeight: 600,
                                            }}
                                        >
                                            FAQ
                                        </h4>
                                    </a>
                                </li>
                                <li>
                                    <a href="https://www.instagram.com/cantrelate.jp/">
                                        <FaInstagram
                                            style={{
                                                width: "30px",
                                                height: "30px",
                                                color: "#fff",
                                                marginRight: "7px",
                                            }}
                                        />
                                    </a>
                                    <a href="mailto:cantrelatejp@gmail.com">
                                        <FaEnvelope
                                            style={{
                                                width: "30px",
                                                height: "30px",
                                                color: "#fff",
                                            }}
                                        />
                                    </a>
                                </li>
                            </ul>
                        </div>
                    </div>
                </div>
            </footer>
            <CopyrightBar />
        </>
    );
};

export default Footer;
