import { useEffect, useState } from "react";
import { useSelector } from "react-redux";
import {
    useCheckoutMutation,
    useGetCurrUserQuery,
    useValidateCouponMutation,
} from "../../generated/graphql";
import { CardElement } from "@stripe/react-stripe-js";

function CardSection() {
    const CARD_ELEMENT_OPTIONS: any = {
        iconStyle: "solid",
        hidePostalCode: true,
        style: {
            base: {
                iconColor: "rgb(240, 57, 122)",
                color: "rgb(240, 57, 122)",
                fontSize: "16px",
                fontFamily: '"Open Sans", sans-serif',
                fontSmoothing: "antialiased",
                "::placeholder": {
                    color: "#CFD7DF",
                },
            },

            invalid: {
                color: "#e5424d",
                ":focus": {
                    color: "#303238",
                },
            },
        },
    };

    return <CardElement options={CARD_ELEMENT_OPTIONS} />;
}

const Checkout = props => {
    const { data } = useGetCurrUserQuery();
    const [checkout] = useCheckoutMutation();
    const [validateCoupon] = useValidateCouponMutation();
    const [discountPH, setDiscountPH] = useState(0);
    const products = useSelector(state => state.productsInCart);
    const [email, setEmail] = useState(data?.getCurrUser.email || "");
    const [name, setName] = useState("");
    const [coupon, setCoupon] = useState("");
    const [subtotal, setSubtotal] = useState(-1);
    const [subtotalWtax, setSubtotalWtax] = useState(-1);
    const [billing, setBilling] = useState({
        refresh: false,
        email: "",
        name: "",
        address: {
            city: "",
            line1: "",
            zip: "",
            state: "",
        },
    });

    const [address, setAddress] = useState({
        city: "",
        line1: "",
        zip: "",
        state: "",
    });

    useEffect(() => {
        var elems = document.querySelectorAll(".autocomplete");
        M.Autocomplete.init(elems, {
            data: {
                Alabama: null,
                Alaska: null,
                Arizona: null,
                Arkansas: null,
                California: null,
                Colorado: null,
                Connecticut: null,
                Delaware: null,
                Florida: null,
                Georgia: null,
                Hawaii: null,
                Idaho: null,
                Illinois: null,
                Indiana: null,
                Iowa: null,
                Kansas: null,
                Kentucky: null,
                Louisiana: null,
                Maine: null,
                Maryland: null,
                Massachusettes: null,
                Michigan: null,
                Minnesota: null,
                Mississippi: null,
                Missouri: null,
                Montana: null,
                Nebraska: null,
                Nevada: null,
                "New Hampshire": null,
                "New Jersey": null,
                "New Mexico": null,
                "New York": null,
                "North Carolina": null,
                "North Dakota": null,
                Ohio: null,
                Oklahoma: null,
                Oregon: null,
                Pennsylvania: null,
                "Rhode Island": null,
                "South Carolina": null,
                "South Dakota": null,
                Tennessee: null,
                Texas: null,
                Utah: null,
                Vermont: null,
                Virginia: null,
                Washington: null,
                "West Virginia": null,
                Wisconsin: null,
                Wyoming: null,
            },
        });
    });

    const handleSubmit = async () => {
        const { stripe, elements } = props;
        if (!stripe || !elements) {
            return;
        }

        const card = elements.getElement(CardElement);
        const result = await stripe.createToken(card);

        if (result.error) {
            console.log("result.error :>> ", result.error);
        } else {
            console.log("result.token :>> ", result.token);
            let tmp: any = address;
            tmp.email = email;
            let products_str = JSON.stringify(products),
                token_str = JSON.stringify(result.token),
                shipping_str = JSON.stringify(tmp);

            let user_id;
            if (!data) {
                user_id = "NONE";
            } else {
                user_id = data.getCurrUser!.user_id!;
            }

            let billing_str = "";
            if (billing.refresh) {
                billing_str = JSON.stringify(billing);
            } else {
                billing_str = "NONE";
            }

            checkout({
                variables: {
                    token: token_str,
                    user_id,
                    products: products_str,
                    shipping_info: shipping_str,
                    billing_info: billing_str,
                    coupon,
                },
            });
        }
    };

    if (subtotal === -1) {
        let tmp_subtotal = 0;

        for (let i = 0; i < products.length; i++) {
            tmp_subtotal += products[i].price * products[i].quantity;
        }

        let tax = Math.round(tmp_subtotal * 0.095);
        console.log("tax :>> ", tax);
        // tmp_subtotal += tax;
        setSubtotal(tmp_subtotal);
        setSubtotalWtax(tmp_subtotal + tax);
    }

    return (
        <div className="row" style={{ height: "100%", margin: "0" }}>
            <div className="col s12 m6 " style={{ minHeight: "93vh" }}>
                <div className="container" style={{ marginTop: "50px" }}>
                    <div className="row">
                        <a href="#/my-cart">
                            <svg
                                style={{ marginRight: "8px" }}
                                focusable="false"
                                width="12"
                                height="12"
                                viewBox="0 0 16 16"
                            >
                                <path
                                    d="M3.417 7H15a1 1 0 0 1 0 2H3.417l4.591 4.591a1 1 0 0 1-1.415 1.416l-6.3-6.3a1 1 0 0 1 0-1.414l6.3-6.3A1 1 0 0 1 8.008 2.41z"
                                    fill-rule="evenodd"
                                ></path>
                            </svg>
                            <span>Back To Cart</span>
                        </a>
                    </div>
                    <div>
                        <div
                            className="ProductSummary container"
                            style={{ marginTop: "32px" }}
                        >
                            <div
                                className="ProductSummary-info"
                                style={{
                                    display: "flex",
                                    flexDirection: "column",
                                }}
                            >
                                <span
                                    style={{
                                        color: "rgba(26,26,26,.6)",
                                        fontWeight: "bold",
                                    }}
                                >
                                    Total
                                </span>
                                <span
                                    style={{
                                        fontSize: "36px",
                                        letterSpacing: "-.03rem",
                                        color: "#303030",
                                    }}
                                >
                                    ${Number(subtotalWtax / 100).toFixed(2)}
                                </span>
                                <span
                                    style={{
                                        marginTop: "32px",
                                        marginBottom: "32px",
                                    }}
                                    id="Products-summary"
                                >
                                    {products.map((_val, i) => {
                                        return (
                                            <div
                                                className="product"
                                                style={{
                                                    marginBottom: "16px",
                                                    display: "flex",
                                                    justifyContent:
                                                        "space-between",
                                                }}
                                            >
                                                <div
                                                    className="product-name"
                                                    style={{
                                                        fontSize: "16px",
                                                        fontWeight: 600,
                                                    }}
                                                >
                                                    {products[i].name}
                                                    {products[i].quantity !==
                                                    1 ? (
                                                        <>
                                                            {" "}
                                                            x
                                                            {
                                                                products[i]
                                                                    .quantity
                                                            }
                                                        </>
                                                    ) : (
                                                        <></>
                                                    )}
                                                </div>
                                                <div
                                                    className="product-subtotal"
                                                    style={{ fontWeight: 390 }}
                                                >
                                                    {Number(
                                                        (products[i].price *
                                                            products[i]
                                                                .quantity) /
                                                            100
                                                    ).toFixed(2)}
                                                </div>
                                            </div>
                                        );
                                    })}
                                    <div
                                        className="subtotal"
                                        style={{
                                            paddingBottom: "16px",
                                            paddingTop: "16px",
                                            display: "flex",
                                            justifyContent: "space-between",
                                        }}
                                    >
                                        <span>Subtotal</span>
                                        <span>
                                            {Number(subtotal / 100).toFixed(2)}
                                        </span>
                                    </div>
                                    <div className="divider"></div>

                                    <div
                                        id="coupon-applied"
                                        style={{
                                            display: "none",
                                            justifyContent: "space-between",
                                        }}
                                    >
                                        <span>Coupon Applied</span>
                                        <span style={{ fontWeight: "bold" }}>
                                            -
                                            {Number(discountPH / 100).toFixed(
                                                2
                                            )}
                                        </span>
                                    </div>
                                    <div className="row" id="promocode-input">
                                        <div className="col s9">
                                            <div className="input-field">
                                                <input
                                                    id="PromoCode"
                                                    type="text"
                                                    className="validate"
                                                    value={coupon}
                                                    onChange={e =>
                                                        setCoupon(
                                                            e.target.value
                                                        )
                                                    }
                                                />
                                                <label htmlFor="PromoCode">
                                                    Add a promotion code
                                                </label>
                                                <span
                                                    id="coupon-helper"
                                                    className="helper-text"
                                                    data-error="Please enter a valid email"
                                                ></span>
                                            </div>
                                        </div>
                                        <div className="col s3">
                                            <button
                                                className="btn"
                                                style={{
                                                    backgroundColor: "#343145",
                                                    marginTop: "25px",
                                                    marginBottom: "15px",
                                                }}
                                                onClick={async () => {
                                                    //validate  coupon
                                                    if (coupon.length !== 0) {
                                                        let type = await validateCoupon(
                                                            {
                                                                variables: {
                                                                    coupon_name: coupon,
                                                                },
                                                            }
                                                        );
                                                        console.log(
                                                            "type :>> ",
                                                            type
                                                        );
                                                        if (
                                                            type.data!
                                                                .validateCoupon ===
                                                            "INVALID"
                                                        ) {
                                                            document
                                                                .getElementById(
                                                                    "PromoCode"
                                                                )!
                                                                .classList.add(
                                                                    "invalid"
                                                                );

                                                            document.getElementById(
                                                                "coupon-helper"
                                                            )!.attributes[
                                                                "data-error"
                                                            ].nodeValue = `The coupon you entered(${coupon}) is not valid`;

                                                            setCoupon("");
                                                        } else if (
                                                            type.data!
                                                                .validateCoupon ===
                                                            "FREE_SHIPPING"
                                                        ) {
                                                            // Handle Free Shipping here
                                                        } else {
                                                            // amount to subtract from subtotal = subtotal * percentage
                                                            let percentage =
                                                                "0." +
                                                                type.data!
                                                                    .validateCoupon;

                                                            let discount = Math.round(
                                                                subtotal *
                                                                    Number(
                                                                        percentage
                                                                    )
                                                            );

                                                            let tmp = subtotal;
                                                            tmp -= discount;

                                                            let tax = Math.round(
                                                                tmp * 0.095
                                                            );

                                                            setSubtotalWtax(
                                                                tmp + tax
                                                            );
                                                            console.log(
                                                                "tax :>> ",
                                                                tax
                                                            );
                                                            setSubtotal(tmp);
                                                            setDiscountPH(
                                                                discount
                                                            );

                                                            // hide promo input and submit button
                                                            document.getElementById(
                                                                "promocode-input"
                                                            )!.style.display =
                                                                "none";
                                                            // replace promo input and submit button with a icon that shows the coupon and discount applied
                                                            document.getElementById(
                                                                "coupon-applied"
                                                            )!.style.display =
                                                                "flex";
                                                        }
                                                    }
                                                }}
                                            >
                                                Submit
                                            </button>
                                        </div>
                                    </div>

                                    <span
                                        style={{
                                            marginTop: "8px",
                                            marginBottom: "16px",
                                            display: "flex",
                                            justifyContent: "space-between",
                                        }}
                                    >
                                        <span>Sales Tax</span>
                                        <span style={{ fontWeight: "bold" }}>
                                            {Number(
                                                Math.round(subtotal * 0.095) /
                                                    100
                                            ).toFixed(2)}
                                            (9.5%)
                                        </span>
                                    </span>

                                    <div className="divider"></div>

                                    <span
                                        style={{
                                            marginTop: "16px",
                                            display: "flex",
                                            justifyContent: "space-between",
                                        }}
                                    >
                                        <span>Total Due</span>
                                        <span>
                                            {Number(subtotalWtax / 100).toFixed(
                                                2
                                            )}
                                        </span>
                                    </span>
                                </span>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div
                className="col s12 m6 z-depth-3 "
                style={{ minHeight: "93vh" }}
            >
                <div className="container">
                    <h2
                        style={{
                            color: "rgba(26,26,26,.9)",
                            fontWeight: 500,
                            fontSize: "16px",
                        }}
                    >
                        Shipping Information
                    </h2>
                    <div className="input-field">
                        <input
                            id="email"
                            type="text"
                            value={email}
                            onChange={e => setEmail(e.target.value)}
                        />
                        <label htmlFor="email">Email</label>
                        <span
                            className="helper-text"
                            data-error="Please your email"
                        ></span>
                    </div>
                    <div className="input-field">
                        <input
                            id="name"
                            value={name}
                            type="text"
                            onChange={e => setName(e.target.value)}
                        />
                        <label htmlFor="name">Name</label>
                        <span
                            className="helper-text"
                            data-error="Please your name"
                        ></span>
                    </div>
                    <div className="input-field">
                        <input
                            id="address"
                            value={address.line1}
                            type="text"
                            onChange={e =>
                                setAddress({
                                    ...address,
                                    line1: e.target.value,
                                })
                            }
                        />
                        <label htmlFor="address">Address</label>
                        <span
                            className="helper-text"
                            data-error="Please your address"
                        ></span>
                    </div>

                    <div className="row">
                        <div className="col s6">
                            <div className="input-field">
                                <input
                                    id="city"
                                    value={address.city}
                                    type="text"
                                    onChange={e =>
                                        setAddress({
                                            ...address,
                                            city: e.target.value,
                                        })
                                    }
                                />
                                <label htmlFor="city">City</label>
                                <span
                                    className="helper-text"
                                    data-error="Please your city"
                                ></span>
                            </div>
                        </div>
                        <div className="col s6">
                            <div className="input-field">
                                <input
                                    id="zip"
                                    value={address.zip}
                                    type="text"
                                    onChange={e =>
                                        setAddress({
                                            ...address,
                                            zip: e.target.value,
                                        })
                                    }
                                />
                                <label htmlFor="zip">Zip</label>
                                <span
                                    className="helper-text"
                                    data-error="Please your zip"
                                ></span>
                            </div>
                        </div>
                    </div>

                    <div className="input-field">
                        <input
                            id="state"
                            value={address.state}
                            className="autocomplete"
                            type="text"
                            onChange={e =>
                                setAddress({
                                    ...address,
                                    state: e.target.value,
                                })
                            }
                        />
                        <label htmlFor="state">State</label>
                        <span
                            className="helper-text"
                            data-error="Please Pick a State"
                        ></span>
                    </div>

                    <div className="divider"></div>

                    <p>
                        <label>
                            <input
                                type="checkbox"
                                id="Billing_input"
                                onClick={() => {
                                    setBilling({
                                        refresh: !billing.refresh,
                                        email: "",
                                        name: "",
                                        address: {
                                            city: "",
                                            line1: "",
                                            zip: "",
                                            state: "",
                                        },
                                    });
                                }}
                            />
                            <span>Billing is different from shipping</span>
                        </label>
                    </p>

                    {billing.refresh ? (
                        <>
                            <h2
                                style={{
                                    color: "rgba(26,26,26,.9)",
                                    fontWeight: 500,
                                    fontSize: "16px",
                                }}
                            >
                                Billing Information
                            </h2>
                            <div className="input-field">
                                <input
                                    id="billing_email"
                                    type="text"
                                    value={billing.email}
                                    onChange={e =>
                                        setBilling({
                                            ...billing,
                                            email: e.target.value,
                                        })
                                    }
                                />
                                <label htmlFor="billing_email">
                                    Billing Email
                                </label>
                            </div>
                            <div className="input-field">
                                <input
                                    id="billing_name"
                                    value={billing.name}
                                    type="text"
                                    onChange={e =>
                                        setBilling({
                                            ...billing,
                                            name: e.target.value,
                                        })
                                    }
                                />
                                <label htmlFor="billing_name">Name</label>
                            </div>
                            <div className="input-field">
                                <input
                                    id="billing_address"
                                    value={billing.address.line1}
                                    type="text"
                                    onChange={e =>
                                        setBilling({
                                            ...billing,
                                            address: {
                                                ...billing.address,
                                                line1: e.target.value,
                                            },
                                        })
                                    }
                                />
                                <label htmlFor="billing_address">Address</label>
                            </div>

                            <div className="row">
                                <div className="col s6">
                                    <div className="input-field">
                                        <input
                                            id="billing_city"
                                            value={billing.address.city}
                                            type="text"
                                            onChange={e =>
                                                setBilling({
                                                    ...billing,
                                                    address: {
                                                        ...billing.address,
                                                        city: e.target.value,
                                                    },
                                                })
                                            }
                                        />
                                        <label htmlFor="billing_city">
                                            City
                                        </label>
                                    </div>
                                </div>
                                <div className="col s6">
                                    <div className="input-field">
                                        <input
                                            id="billing_zip"
                                            value={billing.address.zip}
                                            type="text"
                                            onChange={e =>
                                                setBilling({
                                                    ...billing,
                                                    address: {
                                                        ...billing.address,
                                                        zip: e.target.value,
                                                    },
                                                })
                                            }
                                        />
                                        <label htmlFor="billing_zip">Zip</label>
                                    </div>
                                </div>
                            </div>

                            <div className="input-field">
                                <input
                                    id="billing_state"
                                    value={billing.address.state}
                                    className="autocomplete"
                                    type="text"
                                    onChange={e =>
                                        setBilling({
                                            ...billing,
                                            address: {
                                                ...billing.address,
                                                state: e.target.value,
                                            },
                                        })
                                    }
                                />
                                <label htmlFor="billing_state">State</label>
                            </div>
                        </>
                    ) : (
                        <></>
                    )}
                    <h2
                        style={{
                            color: "rgba(26,26,26,.9)",
                            fontWeight: 500,
                            fontSize: "16px",
                        }}
                    >
                        Payment Details
                    </h2>
                    <CardSection />

                    <div
                        style={{
                            width: "100%",
                            display: "flex",
                            justifyContent: "center",
                        }}
                    >
                        <button
                            className="btn"
                            style={{
                                marginTop: "16px",
                                marginBottom: "16px",
                                width: "380px",
                                backgroundColor: "#343145",
                            }}
                            onClick={() => {
                                if (
                                    !name ||
                                    !email ||
                                    !address.line1 ||
                                    !address.city ||
                                    !address.zip ||
                                    !address.state
                                ) {
                                    if (!email) {
                                        document
                                            .getElementById("email")!
                                            .classList.add("invalid");
                                    }
                                    if (!name) {
                                        document
                                            .getElementById("name")!
                                            .classList.add("invalid");
                                    }

                                    if (!address.line1) {
                                        document
                                            .getElementById("address")!
                                            .classList.add("invalid");
                                    }

                                    if (!address.city) {
                                        document
                                            .getElementById("city")!
                                            .classList.add("invalid");
                                    }

                                    if (!address.zip) {
                                        document
                                            .getElementById("zip")!
                                            .classList.add("invalid");
                                    }

                                    if (!address.state) {
                                        document
                                            .getElementById("state")!
                                            .classList.add("invalid");
                                    }
                                    M.toast({
                                        html: "Please fill in the missing data",
                                        classes: "rounded",
                                    });
                                } else {
                                    handleSubmit();
                                }
                            }}
                        >
                            Pay ${Number(subtotalWtax / 100).toFixed(2)}
                        </button>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default Checkout;
