import { useGetProductsQuery } from "../../generated/graphql";
import "../../css/products.scss";
import { useEffect } from "react";
import anime from "animejs";

const Products = () => {
    const { data, loading } = useGetProductsQuery();

    useEffect(() => {
        anime({
            duration: function (_t, i) {
                return 900 + i * 75;
            },
            targets: ".card-content",
            easing: "easeOutExpo",
            // delay: function (t, i) {
            //     return i * 50;
            // },
            opacity: { value: [0, 1], easing: "linear" },
            scale: [0, 1],
            delay: anime.stagger(150, { start: 100 }),
        });

        anime({
            duration: function (_t, i) {
                return 900 + i * 75;
            },
            targets: ".card",
            easing: "easeOutExpo",
            // delay: function (t, i) {
            //     return i * 50;
            // },
            opacity: { value: [0, 1], easing: "linear" },
            scale: [0, 1],
            delay: anime.stagger(150, { start: 100 }),
        });
    });

    if (loading) {
        return <>loading</>;
    }

    console.log("data :>> ", data?.getProducts);
    return (
        <div className="container">
            <h1
                className="center-align"
                style={{
                    marginTop: "60px",
                    marginBottom: "60px",
                    fontWeight: 600,
                }}
            >
                Products
            </h1>

            <div className="products-grid">
                {data?.getProducts.map((_val, i) => {
                    let product: any = data.getProducts[i];
                    return (
                        <div
                            className="card z-depth-0"
                            style={{ margin: "0px 8px 32px 8px" }}
                            key={i}
                        >
                            <a
                                className="black-text"
                                href={`#/product-details:${product.product_id}`}
                            >
                                <div className="card-image">
                                    <img
                                        src={product.images[0].img_url!}
                                        alt={product.name}
                                        style={{ maxHeight: "280px" }}
                                    />
                                </div>

                                <div className="card-content">
                                    <span className="card-title">
                                        <h5 className="center-align">
                                            {product.name}
                                        </h5>
                                    </span>
                                    <span
                                        style={{
                                            display: "flex",
                                            justifyContent: "center",
                                        }}
                                    >
                                        <p
                                            className="product-price"
                                            style={{ fontSize: "16px" }}
                                        >
                                            $
                                            {Number(
                                                product.price / 100
                                            ).toFixed(2)}
                                        </p>
                                    </span>
                                </div>
                            </a>
                        </div>
                    );
                })}
            </div>
        </div>
    );
};

export default Products;
