import { FaInstagram, FaEnvelope } from "react-icons/fa";
import { useGetAnnouncementsQuery } from "../../generated/graphql";
import anime from "animejs";
import { useEffect } from "react";

const pickIntro = () => {
    const animations = [
        {
            targets: ".announcement-1 .letter",
            translateX: [40, 0],
            translateZ: 0,
            opacity: [0, 1],
            easing: "easeOutExpo",
            duration: 1200,
            delay: (el, i) => 500 + 30 * i,
        },
    ]; //define animations here
    let index = Math.floor(Math.random() * animations.length - 1 + 1);
    return animations[index];
};

const pickOutro = () => {
    const animations = [
        {
            targets: ".announcement-1 .letter",
            translateY: [0, -100],
            opacity: [1, 0],
            easing: "easeInExpo",
            duration: 1200,
            delay: (el, i) => 100 + 30 * i,
        },
    ]; //define animations here
    let index = Math.floor(Math.random() * animations.length - 1 + 1);
    return animations[index];
};

const AnnouncementBar = () => {
    const { data } = useGetAnnouncementsQuery();
    console.log("data =>", data);

    useEffect(() => {
        let timelines = [] as any[];

        if (!!data && !!document.getElementById(`announcement-0`)) {
            var textWrapper = document.getElementById(`announcement-0`);
            textWrapper!.innerHTML = textWrapper!.textContent!.replace(
                /\S/g,
                "<span class='letter'>$&</span>"
            );
            var textWrapper = document.getElementById(`announcement-1`);
            textWrapper!.innerHTML = textWrapper!.textContent!.replace(
                /\S/g,
                "<span class='letter'>$&</span>"
            );
            var textWrapper = document.getElementById(`announcement-2`);
            textWrapper!.innerHTML = textWrapper!.textContent!.replace(
                /\S/g,
                "<span class='letter'>$&</span>"
            );

            let tl = 0;
            anime
                .timeline({
                    loop: true,
                    loopBegin: () => {
                        if (!document.getElementById(`announcement-${tl}`)) {
                            tl = 0;
                        }
                        document.getElementById(
                            `announcement-${tl}`
                        )!.style.display = "block";
                    },
                    loopComplete: () => {
                        document.getElementById(
                            `announcement-${tl}`
                        )!.style.display = "none";
                        tl++;
                    },
                })
                .add({
                    targets: `.announcement .letter`,
                    translateY: [100, 0],
                    translateZ: 0,
                    opacity: [0, 1],
                    easing: "easeOutExpo",
                    duration: 1400,
                    delay: (el, i) => 300 + 30 * i,
                })
                .add(
                    {
                        targets: `.announcement .letter`,
                        translateX: [0, -30],
                        opacity: [1, 0],
                        easing: "easeOutQuart",
                        duration: 1000,
                        delay: (el, i) => 100 + 30 * i,
                    },
                    "+=15000"
                );
        }
    });

    return (
        <div
            style={{
                width: "100%",
                backgroundColor: "black",
                color: "#fff",
            }}
        >
            <div
                className="container"
                style={{ display: "flex", justifyContent: "space-between" }}
            >
                <span className="right black-text noselect">
                    <FaEnvelope />
                    <FaInstagram />
                </span>

                <>
                    {data?.getAnnouncements.length === 0 ? (
                        <>Follow us on our socials</>
                    ) : (
                        <span id="announcement">
                            {data?.getAnnouncements.map((_val, i) => {
                                return (
                                    <span
                                        id={`announcement-${i}`}
                                        className="announcement "
                                    >
                                        {data.getAnnouncements[i].text}
                                    </span>
                                );
                            })}
                        </span>
                    )}
                </>

                <span className="right">
                    <a
                        href="mailto:cantrelatejp@gmail.com"
                        style={{ color: "#fff" }}
                    >
                        <FaEnvelope
                            style={{ marginRight: "5px", marginLeft: "5px" }}
                        />
                    </a>

                    <a
                        href="https://www.instagram.com/cantrelate.jp/"
                        style={{ color: "#fff" }}
                    >
                        <FaInstagram />
                    </a>
                </span>
            </div>
        </div>
    );
};

export default AnnouncementBar;
